import React from "react"
import Favorites from "../../utils/favorites"
import ProgressBar from "react-bootstrap/ProgressBar"
import Button from "react-bootstrap/Button"
import { graphql, navigate, useStaticQuery } from "gatsby"
import Img from "gatsby-image"

const symbolCheck = require("../../utils/symbols")

const LearnWords = ({ words }) => {
  const data = useStaticQuery(graphql`{
  allFile(filter: {relativePath: {regex: "/sounds//"}}) {
    nodes {
      name
      publicURL
    }
  }
  allImageSharp {
    nodes {
      fluid {
        originalName
        ...GatsbyImageSharpFluid
      }
    }
  }
}`);
  const images = data.allImageSharp.nodes
  const setImage = (label) => {
    let imageBase = images.find(item => {
      return item.fluid.originalName === `${label}.png`
        || item.fluid.originalName === `${label}.jpg`
        || item.fluid.originalName === `${label}-1.jpg`
        || item.fluid.originalName === `${label}-2.jpg`
        || item.fluid.originalName === `${label}-3.jpg`
        || item.fluid.originalName === `${label}-4.jpg`
        || item.fluid.originalName === `${label}-5.jpg`
    })
    let image = null;
    if (imageBase) {
      image = <div className="learn-image-wrapper"><Img fluid={imageBase.fluid}/></div>
    }
      return image;
  }

  const favorites = new Favorites();
  const [pos, setPos] = React.useState([0, 1])
  const [labelNext, setLabelNext] = React.useState("Next")
  const [labelPrev, setLabelPrev] = React.useState("Prev")
  const [isFavorite, setIsFavorite] = React.useState(favorites.storageFindIndexWord(words[pos[0]]) !== -1)
  const files = data.allFile.nodes
  if (pos[0] === (words.length - 1)) {
    if (labelNext !== "Finish") setLabelNext("Finish")
  }
  if (pos[0] === 0) {
    if (labelPrev !== "Home") setLabelPrev("Home")
  } else {
    if (labelPrev !== "Prev") setLabelPrev("Prev")
  }
  const next = () => {
    if (pos[0] < words.length - 1) {
      setPos([pos[0] + 1, 1])
      setIsFavorite(favorites.storageFindIndexWord(words[pos[0] + 1]) !== -1)
    } else {
      navigate("/learn")
      return null
    }
  }
  const prev = () => {
    if (pos[0] > 0) {
      setPos([pos[0] - 1, 1])
      setIsFavorite(favorites.storageFindIndexWord(words[pos[0] - 1]) !== -1)
    } else {
      navigate("/learn")
      return null
    }
  }
  const onFavoriteClick = () => {
    if (isFavorite) {
      favorites.storageRemoveWord(words[pos[0]])
      setIsFavorite(!isFavorite)
    } else {
      favorites.storageAddWord(words[pos[0]])
      setIsFavorite(!isFavorite)
    }
  }
  const favoriteLabel = isFavorite ? "❌" : "❤️"
  const favoriteColor = isFavorite ? { backgroundColor: "#ff99cd" } : null

  return (<>

      <ProgressBar now={pos[0] + 1} label={`${pos[0] + 1}/${words.length}`} min="0" max={words.length}/>
      <div className="row">
        <Button className="col mx-3 my-1" style={favoriteColor} variant="outline-light" onClick={onFavoriteClick}
                key={pos}>{favoriteLabel}</Button>
      </div>
      <div className="english-words">
        {setImage(symbolCheck(words[pos[0]][0]))}
        <h1 className="word">{words[pos[0]][0]}</h1>
        <p className="translation">{words[pos[0]][1]}</p>
      </div>
      <div className="row">
        <Button className="col btn btn-success ml-3 my-2" onClick={prev}>{labelPrev}</Button>
        <Button className="col btn btn-warning mx-2 my-2" onClick={() => {
          const audio = files.find(el => el.name === symbolCheck(words[pos[0]][0]))
          if (audio) {
            (new Audio(audio.publicURL)).play()
          } else {
            console.log("no mp3")
          }
        }}>Listen</Button>
        <Button className="col btn btn-success mr-3 my-2" onClick={next}>{labelNext}</Button>
      </div>
    </>
  )
}

export default LearnWords
